
import { defineComponent } from "vue";
import * as moment from "moment-timezone";

export default defineComponent({
  name: "CopyBox",
  props: {
    startingZone: String,
  },
  emits: ["changed"],
  data() {
    return {
      zones: moment.tz.names(),
      selectedZone: this.startingZone,
    };
  },
});
